    font-family: 'Lobster';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('ttf')
}

html, body {
    height: 100%;
    margin: 0;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: 'Lobster', sans-serif;
}

#content {
    flex: 1;
    overflow-y: auto;
}

.header {
    background-color: #f1f5f9;
    border-bottom: 1px solid #e2e8f0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}

.header__headline {
    color: #ffa400;
    font-size: 3rem;
    font-weight: bold;
}

.tab-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.tab-menu__tab {
    font-weight: bold;
    color: #ffa400;
    cursor: pointer;
}

.tab-menu__tab--active {
    color: #555;
}

.home {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.hero-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.hero-section__title {
    font-size: 3rem;
    font-weight: bold;
    color: #ffa400;
    text-transform: uppercase;
    text-align: center;
}

.hero-section__description {
    text-align: center;
    font-size: 1.25rem;
    font-weight: bold;
    color: #ffffff;
}

.menu {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(30rem,100%), 1fr));
    gap: 1.5rem;
    padding: 1rem;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    border: 1px solid #ccc;
    border-radius: 1rem;
    padding: 1rem;
}

.card__name {
    font-size: 1.5rem;
    font-weight: bold;
}

.card__content {
    font-size: 1rem;
    color: #555;
    text-align: center;
}

.card__image {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 0.25rem;
    transition: transform 0.3s ease-in-out;
}

.card__image:hover {
    transform: scale(1.05);
}

.card__price {
    font-size: 1.25rem;
    font-weight: bold;
    color: #e44d26;
}

.contact {
    height: 100%;
}

.contact__headline {
    font-size: 2rem;
    color: #ffa400;
    font-weight: bold;
    text-align: center;
    padding: 1rem;
}

.contact__wrapper {
    display: flex;
    justify-content: center;
    padding: 2rem;
    gap: 2rem;
}

.contact__map {
    width: 100%;
    height: 100%;
    min-height: 300px;
    max-width: 600px;
}

.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
}

.contact-form__input,.contact-form__textarea {
    border: 1px solid #e2e8f0;
    border-radius: 0.5rem;
    padding: 0.75rem 1.25rem;
    font-size: 1rem;
    color: #4b5563;
}

.contact-form__textarea {
    resize: none;
}

.contact-form__input:focus,.contact-form__textarea:focus {
    border: 1px solid #ffa400;
    outline: none;
}

.contact-form__send-btn {
    border: 1px solid #e2e8f0;
    border-radius: 0.25rem;
    color: #4b5563;
    padding: 0.5rem 1rem;
    align-self: end;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
}

.contact-form__send-btn:hover {
    border-color: #ffa400;
}

::-webkit-scrollbar {
    width: 0.5rem;
    height: 0;
}
::-webkit-scrollbar-track {
    border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb {
    background: #edf2f5;
    border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
    background: #bfbfbf;
}

@media screen and (max-width: 640px) {
    .header {
        flex-direction: column;
        gap: 1rem;
        align-items: stretch;
        text-align: center;
    }

    .tab-menu {
        flex-direction: column;
        gap: 1rem;
    }

    .hero-section__title {
        font-size: 2rem;
    }

    .hero-section__description {
        font-size: 1rem;
    }

    .contact__wrapper {
        flex-direction: column;
        gap: 3rem;
    }

    .contact__form,
    .contact__map {
        width: 100%;
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/style/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB;AACJ;;AAEA;IACI,YAAY;IACZ,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,kCAAkC;AACtC;;AAEA;IACI,OAAO;IACP,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,gCAAgC;IAChC,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,aAAa;AACjB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,YAAY;IACZ,iFAA0E;IAC1E,8BAA8B;IAC9B,2BAA2B;IAC3B,yBAAyB;IACzB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,qEAAqE;IACrE,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,iBAAiB;IACjB,sBAAsB;IACtB,sCAAsC;AAC1C;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,cAAc;IACd,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;IACrB,wBAAwB;IACxB,eAAe;IACf,cAAc;AAClB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,yBAAyB;IACzB,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,sBAAsB;IACtB,cAAc;IACd,oBAAoB;IACpB,eAAe;IACf,eAAe;IACf,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;AACA;IACI,qBAAqB;AACzB;AACA;IACI,mBAAmB;IACnB,qBAAqB;AACzB;AACA;IACI,mBAAmB;AACvB;;AAEA;IACI;QACI,sBAAsB;QACtB,SAAS;QACT,oBAAoB;QACpB,kBAAkB;IACtB;;IAEA;QACI,sBAAsB;QACtB,SAAS;IACb;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,sBAAsB;QACtB,SAAS;IACb;;I